import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useEffect, useRef } from 'react'
import gsap from 'gsap'
import { useGSAP } from '@gsap/react'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

import { AcademicData, GradeData } from '../../utils/academic_data'
import LineChart from '../../components/linechart'

import trophy from '../../img/trophy.png'
import gcse from '../../img/gcse.jpg'
import mun_award from '../../img/mun_award.png'
import mun_group_photo from '../../img/mun_group_photo.jpg'
import award_1 from '../../img/award_1.png'
import award_2 from '../../img/award_2.jpg'
import award_3 from '../../img/award_3.png'
import award_4 from '../../img/award_4.png'
import award_5 from '../../img/award_5.png'

gsap.registerPlugin(useGSAP)
gsap.registerPlugin(ScrollTrigger)

export const Academic = () => {
    const [isVisible, setIsVisible] = useState(true)
    const BackAnimation = useRef(null)
    const AwardPhotos = [award_1, award_2, award_3, award_4, award_5]

    useEffect(() => {
        const handleScroll = () => {
          const scrollPosition = window.scrollY;
          const triggerHeight = window.innerHeight * 1;
    
          if (scrollPosition > triggerHeight) {
            setIsVisible(false);
          } else {
            setIsVisible(true);
          }
        };
    
        window.addEventListener('scroll', handleScroll);
    
        return () => {
          window.removeEventListener('scroll', handleScroll);
        };
        
    }, [])

    // ON LOAD

    useEffect(() => {
        const tl = gsap.timeline()
        tl.to('.section-title .word', 0.5, {
            y: 0,
            stagger: 0.1,
            delay: 0.2,
            ease: "power2.out"
        })

        tl.to('.back', 0.2, {
            y: 0,
            delay: 0.1,
            ease: "power2.out"
        }, '<')
    }, [])

    // ON SCROLL

    useEffect(() => {
        const tl_scroll = gsap.timeline()
      
        var intro_words = gsap.utils.toArray('.academic-intro .word')

        intro_words.forEach((word, i) => {
            const anim = tl_scroll.to(word, 0.2, {
                y: 0,
                opacity: 1,
                ease: "power2.out"
            }, i * 0.005)

            ScrollTrigger.create({
                trigger: '.academic-story',
                start: '50% 0%',
                animation: anim,
                once: true
            })
        })

        gsap.fromTo('.section-card', 1, {
            yPercent: -100,
            opacity: 0,
        }, {
            yPercent: 0,
            opacity: 1,
            stagger: 0.2,
            scrollTrigger: {
                trigger: '.section-card',
                start: '0% 0%',
                end: '0% 0%',
                once: true
            }
        })

        const tl_scroll_2 = gsap.timeline()
      
        var gcse_words = gsap.utils.toArray('.gcse .word')

        gcse_words.forEach((word, i) => {
            const anim_2 = tl_scroll_2.to(word, 0.2, {
                y: 0,
                opacity: 1,
                ease: "power2.out"
            }, i * 0.005)

            ScrollTrigger.create({
                trigger: '.gcse',
                start: '0% 70%',
                animation: anim_2,
                once: true
            })
        })

        const tl_scroll_3 = gsap.timeline()
      
        var a_level_words = gsap.utils.toArray('.a-level .word')

        a_level_words.forEach((word, i) => {
            const anim_3 = tl_scroll_3.to(word, 0.2, {
                y: 0,
                opacity: 1,
                ease: "power2.out"
            }, i * 0.005)

            ScrollTrigger.create({
                trigger: '.a-level',
                start: '0% 70%',
                animation: anim_3,
                once: true
            })
        })

        gsap.to('.gcse .blur-anim', 0.5, {
            xPercent: 110,
            scrollTrigger: {
                trigger: '.gcse .blur-anim',
                start: '0% 90%',
                end: '0% 0%',
                once: true
            }
        })

        gsap.to('.mun-photo .blur-anim', 0.5, {
            xPercent: 110,
            stagger: 0.2,
            scrollTrigger: {
                trigger: '.mun-photo .blur-anim',
                start: '0% 90%',
                end: '0% 0%',
                once: true
            }
        })

        const tl_scroll_4 = gsap.timeline()
      
        var a_level_words = gsap.utils.toArray('.mun-award-photo .blur-anim')

        a_level_words.forEach((word, i) => {
            const anim_4 = tl_scroll_4.to(word, 0.5, {
                xPercent: 110,
                ease: "power2.out"
            }, i * 0.005)

            ScrollTrigger.create({
                trigger: '.mun-award-photo .blur-anim',
                start: '0% 90%',
                animation: anim_4,
                once: true
            })
        })
    }, [])

    // ON HOVER

    useEffect(() => {
    
        const tl_1 = gsap.timeline({paused: true})
    
        tl_1.to(`.back .mask .letter`, 0.3, {
          yPercent: -100,
          stagger: 0.03,
          ease: 'power2.out'
        })
    
        tl_1.to(`.back .mask .hidden-letter`, 0.3, {
            yPercent: -100,
            stagger: 0.03,
            ease: 'power2.out'
        }, '<')
    
        BackAnimation.current = tl_1
        
      }, [])

    function onAnimEnter(animation) {

        if (animation) {
            animation.play()
        }
    }
  
    function onAnimLeave(animation) {

        if (animation) {
            animation.reverse()
        }
    }

    return (
        <div className='academic' style={{
            
        }}>
            <div className='section-cover-photo section-1 position-fixed top-0 start-0 vw-100 vh-80' style={{ 
                background: `url(${AcademicData.cover_photo}) no-repeat`,
                display: isVisible ? 'block' : 'none'
            }}/>
            <a className='back raleway-regular' 
                onMouseEnter={() => onAnimEnter(BackAnimation.current)}
                onMouseLeave={() => onAnimLeave(BackAnimation.current)}
                onClick={() => window.history.back()}>
                {
                    AcademicData.back.split(' ').map((word, i) => (
                        <span className='d-inline-flex'>
                            {
                                word.split('').map((letter, i) => (
                                    <span className='mask'>
                                        <span className='letter' key={i}>
                                            {letter}
                                        </span>
                                        <span className='hidden-letter' key={i} >
                                            {letter}
                                        </span>
                                    </span>
                                ))
                            }
                            <span className='space-right'></span>
                        </span>
                    ))
                }
            </a>
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100'></div>
                <h1 className='section-title montserrat-bold'>
                    {
                        AcademicData.title.split(' ').map((word, i) => {
                            return(
                                <span className='mask'>
                                    <span className='word'>
                                        {word}
                                    </span>
                                </span>
                            )
                        })
                    }
                </h1>
                <div className='academic-intro section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <div className='large-chart py-custom'>
                        <LineChart color={AcademicData.color} title={'GPA ALONG THE YEARS'}/>
                    </div>
                    <h1 className='montserrat-bold heading'>
                        My Journey: A Path to Academic Excellence
                    </h1>
                    <p className='raleway-regular'>
                    {
                        AcademicData.my_story.split('//').map((part, i) => (
                            <React.Fragment>
                                {
                                    part.split(' ').map((word, j) => (
                                        <span className='mask' key={j}>
                                            <span className='word' key={j}>
                                                {word}
                                            </span>
                                        </span>
                                    ))
                                }
                            {i < AcademicData.my_story.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                            </React.Fragment>
                        ))
                    }
                    </p>
                </div>

                <h4 className='heading-outside raleway-regular mx-custom'>
                    Here are my academic transcripts for the past three years, showcasing the steady improvement in my performance...
                </h4>

                <div className='section-card-container d-flex align-items-start justify-content-between mx-custom mt-4'>
                    {
                        GradeData.map((grade, i) => {
                            return (
                                <SectionCard 
                                    i={i}
                                    data={grade} />
                            )
                        })
                    }
                </div>

                <div className='gcse section-block d-flex flex-column align-items-center mx-custom'>
                    <h1 className='montserrat-bold heading'>My GCSE Journey: From Struggle to Triumph</h1>
                    <p className='raleway-regular'>
                        {
                            AcademicData.gcse_story.split('//').map((part, i) => (
                                <React.Fragment>
                                    {
                                        part.split(' ').map((word, j) => (
                                            <span className='mask' key={j}>
                                                <span className='word' key={j}>
                                                    {word.startsWith('$') ? <b>{word.replace('$', '')}</b> : word}
                                                </span>
                                            </span>
                                        ))
                                    }
                                {i < AcademicData.my_story.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                                </React.Fragment>
                            ))
                        }
                    </p>
                    <div className='full-width-photo'>
                        <img src={gcse}/>
                        <div className='blur-anim'/>
                    </div>
                    
                </div>

                <div className='a-level section-block d-flex flex-column align-items-center mx-custom mb-5'>
                    <h1 className='montserrat-bold heading'>A-Level Mastery: Building on Success</h1>
                    <p className='raleway-regular'>
                        {
                            AcademicData.a_level_story.split('//').map((part, i) => (
                                <React.Fragment>
                                    {
                                        part.split(' ').map((word, j) => (
                                            <span className='mask' key={j}>
                                                <span className='word' key={j}>
                                                    {word.startsWith('$') ? <b>{word.replace('$', '')}</b> : word}
                                                </span>
                                            </span>
                                        ))
                                    }
                                {i < AcademicData.my_story.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                                </React.Fragment>
                            ))
                        }
                    </p>
                    <h1 className='montserrat-bold heading'>MUN: Model United Nations</h1>
                    <p className='raleway-regular'>
                        {
                            AcademicData.mun_1_story.split('//').map((part, i) => (
                                <React.Fragment>
                                    {
                                        part.split(' ').map((word, j) => (
                                            <span className='mask' key={j}>
                                                <span className='word' key={j}>
                                                    {word.startsWith('$') ? <b>{word.replace('$', '')}</b> : word}
                                                </span>
                                            </span>
                                        ))
                                    }
                                {i < AcademicData.my_story.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                                </React.Fragment>
                            ))
                        }
                    </p>
                    <div className='mun-photo-container d-flex justify-content-between'>
                        <div className='mun-photo'>
                            <img src={mun_award}/>
                            <div className='blur-anim'/>
                        </div>

                        <div className='mun-photo'>
                            <img src={mun_group_photo}/>
                            <div className='blur-anim'/>
                        </div>
                    </div>

                    <p className='raleway-regular'>
                        {
                            AcademicData.mun_2_story.split('//').map((part, i) => (
                                <React.Fragment>
                                    {
                                        part.split(' ').map((word, j) => (
                                            <span className='mask' key={j}>
                                                <span className='word' key={j}>
                                                    {word.startsWith('$') ? <b>{word.replace('$', '')}</b> : word}
                                                </span>
                                            </span>
                                        ))
                                    }
                                {i < AcademicData.my_story.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                                </React.Fragment>
                            ))
                        }
                    </p>
                    <div className='mun-award-container d-flex flex-wrap justify-content-between'>
                        {
                            AwardPhotos.map((img, i) => (
                                <div className='d-flex flex-column align-items-center'>
                                    <div className='mun-award-photo' key={i}>
                                        <img src={img}/>
                                        <div className='blur-anim'/>
                                    </div>
                                    <p className='raleway-regular mun-award-name'>Award: Award Name</p>
                                </div>
                            ))
                        }
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

function SectionCard({i, data}) {
    const DownloadBtnAnimation = useRef([]);

    useEffect(() => {
        DownloadBtnAnimation.current = GradeData.map((grade, i) => {
            const tl = gsap.timeline({paused: true});
            
            tl.to(`.download-btn.btn-${i} .mask .letter`, 0.3, {
                yPercent: -100,
                stagger: 0.03,
                ease: 'power2.out'
            })
    
            tl.to(`.download-btn.btn-${i} .mask .hidden-letter`, 0.3, {
                yPercent: -100,
                stagger: 0.03,
                ease: 'power2.out'
            }, '<')
    
            return tl
        })
    }, [])

    const onAnimEnter = (animations, i) => {
        if (animations[i]) {
            animations[i].play();
        }
    }

    const onAnimLeave = (animations, i) => {
        if (animations[i]) {
            animations[i].reverse();
        }
    }

    return (
        <div className='section-card-mask'>
            <div className='section-card'>
                {
                    i == 2 && 
                    <div className='with-high-honors raleway-regular d-flex flex-wrap align-items-center'>
                        <img src={trophy}/>
                        <p>With High Honors!</p>
                    </div>
                }
                <div className='section-card-header d-flex align-items-end justify-content-around'>
                    <div className='left'>
                        <h5 className='raleway-regular'>{data.grade}</h5>
                        <div className='d-flex'>
                            <h1 className='montserrat-bold'>{data.gpa}</h1>
                        </div>  
                    </div>
                    <div className='right d-flex justify-content-end'>
                        <h4 className='raleway-regular'>{data.year}</h4>
                    </div>
                </div>
                <div className='section-card-content d-flex flex-column'>
                    <img src={data.img}/>
                    <a className={`download-btn btn-${i} w-75 raleway-regular`}
                        href={data.pdf}
                        download={data.pdf_name}
                        onMouseEnter={() => onAnimEnter(DownloadBtnAnimation.current, i)}
                        onMouseLeave={() => onAnimLeave(DownloadBtnAnimation.current, i)}
                    >
                        {
                            AcademicData.download_btn.split(' ').map((word, i) => (
                                <span className='d-inline-flex'>
                                    {
                                        word.split('').map((letter, i) => (
                                            <span className='mask'>
                                                <span className='letter' key={i}>
                                                    {letter}
                                                </span>
                                                <span className='hidden-letter' key={i} >
                                                    {letter}
                                                </span>
                                            </span>
                                        ))
                                    }
                                    <span className='space-right'></span>
                                </span>
                            ))
                        }
                    </a>
                </div>
            </div>
        </div>
    )
}